.section-home {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  background-color: black;
}

.section-center {
  flex: 1;
  display: grid;
  /* grid-template-columns: 1fr 2.5fr 1fr; */
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: 1fr;
  gap: 4px;
  /* border: 1px solid red; */
}

.home-article {
  display: flex;
  flex-direction: column;
}

.img-center {
  flex: 1;
  position: relative;
  cursor: pointer;
  z-index: 999;
  /* border: 1px solid red; */
}

.img-center img {
  position: absolute;
  /* max-height: 100%; */
  height: 100%;
  width: 100%;
  object-fit: fill;
  /* object-fit: cover; */
  border-radius: 0.25rem;
  cursor: pointer;
  /* border: 1px solid red; */
}

.hiddenSlide {
  display: none;
}
.lastSlide {
  order: 1;
  opacity: 0.2;
}
.activeSlide {
  order: 2;
}
.nextSlide {
  order: 3;
  opacity: 0.2;
}

.lastSlide .home-title,
.nextSlide .home-title {
  /* visibility: hidden; */
  display: none;
}
.activeSlide .home-title {
  flex: 0 0 110px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  /* font-size: 1.5rem; */
  text-align: center;
  /* border: 1px solid red; */
}
.index-container {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  /* transition: all 0.3s linear; */
  /* overflow: hidden; */
  /* border: 1px solid red; */
}
.prev-indexbtn,
.next-indexbtn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  height: 300px;
  width: 80px;
  background-color: transparent;
  border-color: transparent;
  color: #fff;
  opacity: 0.3;
  transition: all 0.3s linear;
  cursor: pointer;
  /* border: 1px solid red; */
}
.prev-indexbtn {
  left: 0;
  justify-content: flex-start;
}
.next-indexbtn {
  right: 0;
  justify-content: flex-end;
}
.prev-indexbtn:hover,
.next-indexbtn:hover {
  opacity: 1;
}
@media screen and (max-width: 675px) {
  .section-home {
    width: 95vw;
    margin: 0 auto;
    margin-bottom: 0.35rem;
    /* border: 1px solid red; */
  }

  .activeSlide .home-title {
    font-size: 1.25rem;
  }
  .prev-indexbtn,
  .next-indexbtn {
    font-size: 4rem;
    /* border: 1px solid red; */
  }
}
@media screen and (min-width: 675.01px) {
  .section-home {
    width: 95vw;
    /* width: 100vw; */
    /* margin: 0.25rem 1.5rem; */
    /* margin-bottom: 0.5rem; */
    /**/
    margin: 0 auto;
    margin-bottom: 0.5rem;
    /* border: 1px solid red; */
  }

  .activeSlide .home-title {
    font-size: 1.375rem;
  }
  .prev-indexbtn,
  .next-indexbtn {
    font-size: 4.5rem;
    /* border: 1px solid red; */
  }
}
@media screen and (min-width: 768px) {
  .img-center img {
    /* max-width: 80vw; */
    /* height: auto; */
    /* border: 1px solid red; */
  }
}
@media screen and (min-width: 900.01px) {
  .section-home {
    /* margin: 0 1.5rem; */
    /* margin: 0.5rem 1.5rem; */
    /**/
    width: 95vw;
    margin: 0 auto;
    margin-bottom: 0.5rem;
  }

  .activeSlide .home-title {
    font-size: 1.5rem;
  }

  .prev-indexbtn,
  .next-indexbtn {
    font-size: 5rem;
  }
}
