.single-movie-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  background-color: #000;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 0.5rem;
  transition: all 0.3s linear;
}
.single-movie-section:hover {
  border: 1px solid rgba(255, 255, 255, 1);
}
.single-title {
  display: flex;
  justify-content: center;
  align-items: center;
  /**/
  color: #fff;
}
.single-movie {
  flex: 1;
  padding: 0.5rem;
  /* border: 1px solid rgba(255, 255, 255, 0.2); */
  border-radius: 0.25rem;
  overflow: auto;
}
.single-center {
  border-radius: 0.25rem;
}
.single-img {
  display: block;
  object-fit: fill;
  border-radius: 0.25rem;
}
.movie-info {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

.movie-info::-webkit-scrollbar {
  display: none; /* Chrome , Safari , Opera */
}

.movie-info {
  /* border: 1px solid red; */
  border-radius: 0.25rem;
  overflow: auto;
}
.movie-info p {
  font-weight: bold;
  text-transform: uppercase;

  /* border: 1px solid red; */
  /**/
  color: #fff;
}
.movie-data {
  font-weight: 100;
  text-transform: capitalize;
  /* border: 1px solid red; */
  /**/
  color: #fff;
}
@media screen and (max-width: 675px) {
  .single-movie-section {
    padding: 0.5rem;
    /* margin-bottom: 0.5rem; */
    /* border: 1px solid red; */
  }
  .single-title {
    font-size: 1.5rem;
    padding: 0.25rem;
    margin-bottom: 0.5rem;
    /* border: 1px solid red; */
  }
  .single-movie {
    display: flex;
    flex-direction: column;
  }
  .single-center {
    flex: 0 0 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: 1px solid blue; */
  }
  .single-img {
    width: 100%;
    max-width: 500px;
    height: 300px;
  }
  .movie-info {
    flex: 1;
    padding: 0.25rem;
    margin: 0.25rem 0;
    /* border: 1px solid red; */
  }
  .movie-info p {
    font-size: 1.1rem;
    margin-bottom: 0.25rem;
    /* border: 1px solid red; */
  }
  .movie-data {
    font-size: 1.1rem;
  }
}
@media screen and (min-width: 675.01px) and (max-width: 900px) {
  .single-movie-section {
    max-height: calc(100vh - 170px);
    padding: 0.75rem;
    /* border: 1px solid blue; */
  }
  .single-title {
    /* font-size: 2rem; */
    font-size: 1.75rem;
    padding: 0.5rem;
    margin-bottom: 0.75rem;
    /* border: 1px solid blue; */
  }
  .single-movie {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
  }
  .single-center {
    position: relative;
    /* max-width: 600px; */
    /* border: 1px solid blue; */
  }
  .single-img {
    position: absolute;
    width: 100%;
    height: 100%;
    max-width: 500px;
  }
  .movie-info {
    padding: 0.5rem;
    /* border: 1px solid blue; */
  }
  .movie-info p {
    font-size: 1.2rem;
    margin-bottom: 0.35rem;
    /* border: 1px solid blue; */
  }
  .movie-data {
    font-size: 1.2rem;
    /* border: 1px solid blue; */
  }
}

@media screen and (min-width: 900.01px) {
  .single-movie-section {
    max-height: calc(100vh - 170px);
    padding: 1rem 0.75rem;
    /* border: 1px solid red; */
  }
  .single-title {
    font-size: 2rem;
    padding: 0.5rem;
    margin-bottom: 1rem;
    /* border: 1px solid red; */
  }
  .single-movie {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
  }
  .single-center {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .single-img {
    position: absolute;
    width: 100%;
    height: 100%;
    max-width: 600px;
  }
  .movie-info {
    padding: 0.5rem;
    /* border: 1px solid red; */
  }
  .movie-info p {
    font-size: 1.3rem;
    margin-bottom: 0.5rem;
    /* border: 1px solid red; */
  }
  .movie-data {
    font-size: 1.3rem;
  }
}
